/**
 * @description 单个项目展示
 * @author zhghxy
 * @date 2020-03-21
 * @class ProjectCard
 * @extends {React.Component}
 */
class ProjectCard extends React.Component {
    render() {
        const title = this.props.title;
        const mm = this.props.title.match(/[\n\r]+/);
        const index = mm['index'];
        return (
            <li>
                <div >
                    <span className="badge badge-pill badge-secondary">
                        {title.slice(0, index)}
                    </span>{this.props.time}
                    <p>
                        {title.slice(index)}
                    </p>
                </div>
            </li>
        )
    }
}


/**
 * @description 项目展示页面
 * @author zhghxy
 * @date 2020-03-21
 * @export
 * @class Project
 * @extends {React.Component}
 */
export default class Project extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            project_list: [],
            load: false,
            error: false,
            error_msg: 'error'
        }
    }
    componentDidMount() {
        const time_test = /((\d{4})年(\d{1,2}月)?[-至](\d{4})年(\d{1,2}月)?)|((\d{4})\.\d{1,2}\.\d{1,2}[-至](\d{4})\.\d{1,2}\.\d{1,2})[\n\r]*$/;
        genPromise('GET', 'http://localhost/info/project/project.php').then(
            (data) => {
                if (data.code === 0) {
                    data.data.forEach((el) => {
                        var project_time = el.project_name.match(time_test);
                        console.log(project_time);
                        el.time = project_time[2] || project_time[7] || new Date().getFullYear();
                        el.project_name = el.project_name.slice(0, project_time['index']);
                        el.project_time = project_time[0];
                    })
                    data.data.sort((a, b) => {
                        return b.time - a.time;
                    })
                    //console.log(data.data);
                    this.setState({
                        project_list: data.data,
                        load: true
                    })
                } else {
                    this.setState({
                        error: true
                    })
                }
            }, (error) => {
                console.log(error);
                this.setState({
                    error: true,
                    error_msg: error.message
                })
            }
        )
    }

    render() {


        const project_list = this.state.project_list.map((el, index) =>
            <ProjectCard key={index}
                title={el.project_name}
                time={el.project_time}
                abstract={el.abstract}
                tag={el.tag}
                link={el.link} />
        );
        const ContentLoader = () =>
            (<div className='content-loader p-2'>
                <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                    style={{ width: '85%', height: '.8rem' }}></div>
                <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                    style={{ width: '60%', height: '.8rem' }}></div>
                <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                    style={{ width: '60%', height: '.8rem' }}></div>
            </div>);
        if (this.state.error) {
            return (
                <div><Header />
                    <div className='container project-container'>

                        <ResultView text={this.state.error_msg} />
                    </div></div>
            )
        }
        else if (this.state.load) {
            return (
                <div> <Header />
                    <div className='container project-container'>

                        <h4 className='title border-bottom'>项目</h4>
                        <ul>
                            {project_list}
                        </ul>
                    </div></div>
            );
        } else {
            return (
                <div><Header />
                    <div className='container project-container'>

                        <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                            style={{ width: '25%', height: '.8rem' }}></div>
                        <ContentLoader />
                        <ContentLoader />
                        <ContentLoader />
                    </div></div>

            )
        }

    }
}